@import "mixins";

$color-min: #c25d5d;
$color-sec: #5f98a8;
$arrowBlock: 30px;
$arrowBlockCenter: calc(50% - 15px);

body {
  margin: 0;
  display: flex;
  min-height: 100vh;
  align-items: center;
}

.container {
  max-width: 900px;
  margin: 0 auto;
}

.clock {
  position: relative;
  box-sizing: border-box;
  width: 800px;
  height: 800px;
  border-radius: 50%;
  border: 1px solid black;
  
  &__min {
    @include arrows-block;
    background-color: $color-min;
    animation: clock 3600s linear infinite;

    &::after {
      @include arrows;
      height: 300px;
      background-color: $color-min;
    }
  }

  &__sec {
    @include arrows-block;
    background-color: $color-sec;
    animation: clock 60s linear infinite;
    z-index: 1;

    &::after {
      @include arrows;
      height: 380px;
      background-color: $color-sec;
    }
  }
}

@keyframes clock {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
